.historyContainer {
  background-color: white;
  box-shadow: 0 10px 20px 0 rgba(0, 0, 0, 0.1);
  border-radius: 20px;
  padding: 30px;
  margin-left: 50px;

  @media screen and (max-width: 991px) {
    margin-left: 0;
  }

  h2 {
    font-weight: 800;
    font-size: 20px;
    color: #3A8195;
  }

  ul {
    margin: 0;
    padding-left: 20px;

    a {
      font-size: 12px;
    }
  }
}
